.Parentbox {
  width: 80%;
  overflow: hidden;
  margin: auto;
  margin-top: 100px;
}
.Foto {
  width: 50%;
  height: 700px;
  background: #cd061d;
  float: left;
}
.Foto img {
  width: 100%;
}
.Deskripsi {
  width: 40%;
  overflow: hidden;
  float: left;
  margin-left: 30px;
}
.Deskripsi .Info {
  line-height: 1.8;
  color: rgb(18, 11, 49)
}
.Deskripsi .Price {
  font-size: 20px;
  font-weight: 500;
}
.Deskripsi .Title {
  margin: 0;
}
.Deskripsi .Cate {
  color: #acaeb2;
}
.Deskripsi a {
  margin-top: 20px;
  color: #fff;
  font-weight: 500;
  background: #cd061d;
  text-decoration: none;
  display: inline-block;
  padding: 10px 20px 10px 20px;
  font-size: 20px;
  border-radius: 4px;
  transition: all 0.8s;
}
.Deskripsi a:hover {
  color: #ffffff;
  background: rgb(145, 1, 12);
  transition: all 0.8s;
}